import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Schedule from '@/components/Schedule'
import Places from '@/components/Places'
import People from '@/components/People'
import Pinlock from '@/components/Pinlock'
import Login from '@/components/Login'
import Rides from '@/components/Trips'
import Ride from '@/components/Trip'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/pinlock',
      name: 'Pinlock',
      component: Pinlock
    },
    {
      path: '/places',
      name: 'Places',
      component: Places
    },
    {
      path: '/schedule',
      name: 'Schedule',
      component: Schedule
    },
    {
      path: '/people',
      name: 'People',
      component: People
    },

    {
      path: '/rides',
      name: 'rides',
      component: Rides
    },
    {
      path: '/ride/:token_id',
      name: 'ride',
      component: Ride
    }
  ]
})



