import chofa from '../../api/chofa'

// initial state
const state = {
  all: [],
  currentrip: {
    trip_url: "",
    user_id: '',
    source: "",
    tripToken: "",
    destination: "",
    traveller_code: '',
    approver_code:  '',
    status: "pending",
    clientadmin_code: "",
    tripCapacity: 4,
    mapInit: 'initial',
    client_phone: '',
    phone: {
      client: '',
      clientAdmin: '',  
         },
    clients: "null",
    driver: {
      car: {
        capacity: 4,
        make: '',
        plates: ''
      }
    },
    distance: '0',
    minutes: '0',
    cost: "0",
    paid: "false",
    trip_type: "now",
    sourcePoint: null,
    destinationPoint: null,
    scheduled_at: 'now',
    human_date: 'Leave now',
    description: "",
    has_prefered: false,
    clients_arr: [],
    sourcePlaceID: '',
    destinationPlaceID: '',
    trip_id: ''
  },
  test: 'THIS IS THE INITIAL STATE',
  myTrips: []
}

// getters
const getters = {
    allTrips: state => state.all,
    getTest: state => state.test,
    getCurrentTrip: state => state.currentrip,
    getHistory: state => state.historyTrips
}

// actions
const actions = {
  
  getAllTrips ({ commit },token) {
    chofa.getTrips(token,trips => {
      commit('setTrips', trips)
    })
  },
  testActions ({ commit }) {
      commit('testMutation')
  },
  saveRide ({commit}, data){
   
    chofa.saveTripSQL(data,res => {
      commit('SAVE_TRIP_H',res)
    })
   
  }
}

// mutations
const mutations = {
  
  setTrips (state, trips) {
    // let editedtrips = []
    // trips.forEach(element => {
    //     var prd =  {"id": element.id, "title": element.prd_name, "price": element.prd_price, "inventory": element.inventory,description: element.prd_description, category: element.category_id, "url": element.image_url}
    //     editedtrips.push(prd);

    // });
    state.all = trips
  },
  testMutation (state) {
    state.test = 'Aha Changed the muataion'

  },
 SAVE_TRIP_H (state, trip) {
   console.log(trip)
   state.currentrip.trip_id = trip.id
    // state.historyTrips.push(payload.trips);
  },
}

export default {
  state,
  getters,
  actions,
  mutations
}



