<template>
   <md-app style="height: 100vh !important">
        <md-app-toolbar class="md-transparent" md-elevation="0">
        <md-button class="md-icon-button toggle-button">
         <router-link to="/home"> <md-icon>arrow_back</md-icon> </router-link>
        </md-button>
        <span class="md-title">Schedule</span>
      </md-app-toolbar>
      <md-app-content>
          <md-card class="md-elevation-0">
        <md-card-area md-inset>
          <md-card-content >
             
                  <div class="md-layout md-gutter">
            
            <div class="md-layout-item">
                 <md-ripple>
              <md-list>
                <md-list-item @click="toggleRadio('now')">
                    <div class="md-list-item-text">
                    <span>Leave now</span>
                    <span><small>Depart immediately</small></span>
                    </div>
                </md-list-item>
                </md-list>
                 </md-ripple>
             </div>
             <div class="md-layout-item md-size-15"><md-radio v-model="radio" value="now"></md-radio></div>
          </div>
             
          
        </md-card-content>
        </md-card-area>
        <md-card-content >
            
                <div class="md-layout md-gutter">
            
            <div class="md-layout-item">
                <md-ripple>
                    <md-list>
                <md-list-item @click="toggleRadio('schedule')">
                    <div class="md-list-item-text">
                    <span>Schedule</span>
                     <span><small>{{ humanDate }}</small></span>
                    </div>
                </md-list-item>
                </md-list>
                 </md-ripple>
            </div>
             <div class="md-layout-item md-size-15"><md-radio v-model="radio" value="schedule"></md-radio></div>
            
             </div>
           
             
        
                
        </md-card-content>
        <md-card-content v-show="showDate">
          <div class="md-layout md-gutter">
            
            <div class="md-layout-item">
             Schedule a Ride between 1 hour and 2 weeks in advance
               <datetime v-model="date"
                        type="datetime"
                        placeholder="Select date"
                        input-format="DD-MM-YYYY HH:mm"
                        class="theme-orange"
                        input-class="form-control"
                        value-zone="Africa/Nairobi"
                        zone="Africa/Nairobi"
                        :format="{ year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: '2-digit', timeZoneName: 'short' }"
                        :phrases="{ok: 'Continue', cancel: 'Exit'}"
                        :minute-step="10"
                        :min-datetime="minDatetime"
                        :max-datetime="maxDatetime"
                        :week-start="7"
                        use12-hour
                        auto
                    ></datetime>
               
             </div>
          </div>
        </md-card-content>
          </md-card>
         <div class="bottom-bar" @click="confirmSchedule">
             <md-card md-with-hover style="background: #020202;color: #FFEB3B;margin-top: 5px;"  >
       <md-ripple>
      <md-card-content>
        Confirm
      </md-card-content>
       </md-ripple>
    </md-card>
         </div>
      </md-app-content>
   </md-app>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { Datetime } from "vue-datetime";
import { DateTime as LuxonDateTime } from "luxon";
import "./utils/vue-datetime.css";
import localforage from 'localforage';

export default {
    components: {
         datetime: Datetime
    },
    mounted(){
        let l = LuxonDateTime.fromISO(this.date);
        this.humanDate = l.toLocaleString(LuxonDateTime.DATE_HUGE);
        localforage.getItem('access_token').then((value) => {
          console.log('oh say can you see, ' + value);
          if(value == null){
              this.$router.push('/')
          }else{
            // alert('Unauthorised')
          }

        }).catch((err) => {
          console.log('the rockets red glare has blinded me');
        });
    },
    watch: {
        radio(val){

            if(val == 'schedule'){
                this.showDate = true
            }else{
                this.showDate = false
            }
        },
        date(val){
            let l = LuxonDateTime.fromISO(val);
            this.humanDate = l.toLocaleString(LuxonDateTime.DATE_HUGE);
        }
    },
    data() {
        return {
            radio: 'now',
            minDatetime: LuxonDateTime.local().plus({hours: 1}).toISO(),
            maxDatetime: LuxonDateTime.local().plus({days: 14}).toISO(),
            date: LuxonDateTime.local().plus({hours: 2}).toISO(),
            showDate: false,
            humanDate: 'immediately'

        }
    },
      computed:{
    ...mapGetters({
      currentTrip: 'getCurrentTrip',
    })
  },
    methods: {
        toggleRadio(val) {
            this.radio = val
            console.log('============RADIO======================');
            console.log(val);
            console.log('====================================');
        },
        confirmSchedule(){
            let l = LuxonDateTime.fromISO(this.date);
            
            if(this.radio == 'schedule'){
                this.currentTrip.scheduled_at = this.date
                this.currentTrip.human_date = l.toLocaleString(LuxonDateTime.DATETIME_HUGE)
                this.currentTrip.trip_type = "future";
            }else{
                this.currentTrip.scheduled_at = 'now'
                this.currentTrip.human_date = "Leave now"
                this.currentTrip.trip_type = "now";
            }

            this.$router.push('/home')

            // this.humanDate = l.toLocaleString(LuxonDateTime.DATE_HUGE);
        }
    }

}
</script>

<style scoped>
.md-app .md-content.md-theme-default {
    margin: 0 !important;
}
*:focus { outline: none !important; }
li {
    display: inline-block;
    margin: 0 !important;
}
* {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);  
} 
.md-app {
    display: -webkit-box;
    display: flex;
    overflow: hidden;
    position: relative;
    height: 100vh !important;
}
.md-app-content .md-card {
    margin-right: 0 !important;
    margin-left: 0 !important;
    overflow: visible;
}
.md-app-content {
    padding: 0px !important;
}
.bottom-bar {
	position: fixed;
    bottom: 10px;
    width: 90%;
    margin: 5%;
    text-align: center;
	/* background: #000; */
}
.form-control {
    width: 100%;
    padding: 10px;
    margin: 4px;
    border: 1px solid #f2f2f2;
    background: #f2f2f2;
}
</style>



