<template>
   <md-app>
      <md-app-content>
          <div id="mapmy" style="margin-top: 0px !important">
				
			</div>
            <div class="pin-lock">
  
        <div class="pin">
  
          <span style="color: gold;
  
      
              font-family: monospace;
  
          font-weight: bolder;">
  
      5<br>
  
           Min
  
           </span>

        </div>
  
       
        <div class="arrow-down"></div>
    </div>
    <div class="bottomPin" @click="toggleSetPin()"  v-show="pin_show">
        <div class="card-box">
        {{ location_details }}
      </div>
        <md-card md-with-hover style="background: #000000;color: #FFF; margin-top: 5px;">
       <md-ripple>
    
      <md-card-content>
        Done
      </md-card-content>
       </md-ripple>
    </md-card>
    </div>
     
          <md-card v-show="!pin_show">
                 <md-card-area md-inset>
          <md-card-content>
          <div class="md-layout md-gutter">
            <div class="md-layout-item md-size-15">
               <md-button class="md-icon-button toggle-button">
         <router-link to="/home"> <md-icon>arrow_back</md-icon> </router-link>
        </md-button>
            </div>
            <div class="md-layout-item">
                <div class="pac-card" id="pac-card">
                    <div id="pac-container">
                        <input id="pac-input" class="form-control" ref="source" :value="this.currentTrip.source" type="text" @focus="$event.target.select()" @click="setIn('source')" placeholder="Enter pick up point">
                    </div>
                </div>
               <!-- <input class="form-control" ref="source" type="text" placeholder="Pickup Address"> -->
             </div>
          </div>
           <div class="md-layout md-gutter">
            <div class="md-layout-item md-size-15"><md-button class="md-icon-button" disabled>
        <md-icon class="md-accent">place</md-icon>
      </md-button></div>
            <div class="md-layout-item">
               <div class="pac-card" id="pac-card-destination">
                    <div id="pac-container">
                        <input id="pac-input-destination" class="form-control" :value="this.currentTrip.destination" ref="destination" @focus="$event.target.select()" @click="setIn('destination')" type="text" placeholder="Enter Destination">
                    </div>
                </div>
               <!-- <input class="form-control" ref="source" type="text" placeholder="Drop Off Address"> -->
            </div>
            <!-- <div class="md-layout-item md-size-15">
               
      <looping-rhombuses-spinner
        :animation-duration="1000"
        :rhombus-size="12"
        color="#000000"
        style="top: 40%"
        />
        </div> -->
          </div>
        </md-card-content>
        </md-card-area>
          </md-card>
          <md-card v-show="!pin_show">
              <md-card-content style="padding:  0px !important;">
                     <md-list class="md-dense">
                        <md-list-item @click="toggleSetPin()">
                           <md-icon>nature</md-icon>
                           <span class="md-list-item-text">Set pin location on map</span>
                        </md-list-item>
                        </md-list>
              </md-card-content>
          </md-card>
          <md-card class="md-elevation-1" v-show="!pin_show">
              <md-card-content>
                <md-tabs md-alignment="fixed">
                <md-tab id="tab-home" md-icon="history">
                       <md-list v-if="history_trips.length > 0">
                        <div v-for="trip in history_trips.slice(0, 5)" :key="trip.id">
                        <md-list-item @click="selectHistoryTrip(trip)" style="width: 100% !important">
                            <md-icon>history</md-icon>
                            <div class="md-list-item-text">
                             <span>{{ trip.destination}}</span>
                             <span>{{ trip.source}}</span>
                            </div>
                           
                        </md-list-item>

                        <md-divider></md-divider>
                        </div>
                        </md-list>
                <md-empty-state
                    v-if="history_trips.length < 1"
                    md-icon="history"
                    md-label="My Trips"
                    md-description="Hi! your frequent Trips will appear here.">
                    </md-empty-state>

                </md-tab>
                <md-tab id="tab-pages" md-icon="flight_takeoff">
                   <md-empty-state
                   v-show="flightempty"
                    md-rounded
                 
                    md-icon="flight_takeoff"
                    md-label="Nearby Airports"
                    md-description="Hi! nearby airports will appear here.">
                    </md-empty-state>
                     <md-list class="md-dense" v-if="!flightempty">
                        <md-list-item v-for="airport in NearbyAirports" :key="airport.place_id" @click="setPoint(airport)">
                            <md-icon>flight_takeoff</md-icon>
                            <span class="md-list-item-text">{{ airport.name }}</span>
                        </md-list-item>
                        
 

                        </md-list>
                </md-tab>
                <md-tab id="tab-favorites" md-icon="business">
                     <md-empty-state
                   v-show="hotelempty"
                    
                    md-rounded
                    md-icon="business"
                    md-label="Nearby Hotels"
                    md-description="Hi! Nearby hotels appear here.">
                    </md-empty-state>
                    <md-list class="md-dense" v-if="!hotelempty">
                        <md-list-item v-for="hotel in NearbyHotels" :key="hotel.place_id" @click="setPoint(hotel)">
                            <md-icon>business</md-icon>
                            <span class="md-list-item-text">{{ hotel.name }}</span>
                        </md-list-item>
                        


                        </md-list>
                    </md-tab>
                </md-tabs>
              </md-card-content>
          </md-card>
 
      </md-app-content>
   </md-app>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import GoogleMapsLoader from "google-maps";
require('../assets/fonts/font-awesome-4.2.0/css/font-awesome.min.css');
import getLocation from "./utils/GeoLocation";
import Simplert from 'vue2-simplert'
import styles from "./utils/styles";
import { FulfillingSquareSpinner } from 'epic-spinners'
import { LoopingRhombusesSpinner } from 'epic-spinners'
import { HollowDotsSpinner } from 'epic-spinners'
import { FulfillingBouncingCircleSpinner } from 'epic-spinners'
import localforage from 'localforage';
import firebase from '../firebase'

GoogleMapsLoader.REGION = "KE";
GoogleMapsLoader.KEY = "AIzaSyBwNhtFQMlUzChkztqeQ3dI9Xz4CNEWEP0";
GoogleMapsLoader.LIBRARIES = ["geometry", "places"];
export default {
    components: {
        FulfillingSquareSpinner,
        LoopingRhombusesSpinner,
        HollowDotsSpinner,
        FulfillingBouncingCircleSpinner
    }, 
    computed:{
    ...mapGetters({
      trips: 'allTrips',
      currentTrip: 'getCurrentTrip',
    })
  },
  watch: {
     
      trips(val){
        //  console.log('=======Changed TRips========')
        // console.log(val)
        // console.log('=======END OF SOMETHING GREAT========')
        localforage.setItem('history_trips',val).then((value) => {
                console.log('woot! we saved history trips');
              
              }).catch((err) => {
                //  self.$router.push('home')
              });
      },
    },
 data() {
     return {
         mymap: null,
         pin_show: false,
      myLocmarker: null,
      NearbyAirports: [],
      NearbyHotels: [],
      pin: null,
      flightempty: true,
      temp_place: null,
      hotelempty: true,
      input_focussed: 'destination',
      location_details: '',
      history_trips: [],
     }
 },
 mounted(){
     this.$store.dispatch('getAllTrips')
     console.log("===========AYEEEE==================")
     console.log(this.trips)
     console.log("=============================")
      this.$refs.destination.focus();
      this.location_details = this.currentTrip.source;
 localforage.getItem('history_trips').then((value) => {
         
          if(value == null){
              console.log('NOthing in history trips, duh')
          }else{
            this.history_trips = value.sort(function(a,b) {return (a.id < b.id) ? 1 : ((b.id < a.id) ? -1 : 0);} )
            console.log(this.history_trips)
          }

        }).catch((err) => {
          console.log('the rockets red glare has blinded me');
        });

      localforage.getItem('access_token').then((value) => {
          console.log('oh say can you see, ' + value);
          if(value == null){
              this.$router.push('/')
          }else{
            // alert('Unauthorised')
          }

        }).catch((err) => {
          console.log('the rockets red glare has blinded me');
        });
 },
 methods: {
     ...mapActions(['saveTrip']),
    
    //  storePlace(place){
    //     localforage.setItem('my array', [1, 2, 'three']).then(function(value) {
    //         // This will output `1`.
    //         console.log(value[0]);
    //     }).catch(function(err) {
    //         // This code runs if there were any errors
    //         console.log(err);
    //     });
    //  },
     setIn(val){
         console.log(val);
         this.input_focussed = val
     },
     toggleSetPin(){
         this.pin_show = !this.pin_show
     },
     selectHistoryTrip(trip){
         console.log("THIS IS WHAT IS NULL")
         console.log(trip)

         let tripRef = firebase.database().ref('/trips/' + trip.token+ '/');
            
           tripRef.on('value', (snap)=> {
               console.log(snap.val())
                this.currentTrip.source = snap.val().source
                this.currentTrip.destination = snap.val().destination
                this.currentTrip.sourcePoint = snap.val().sourcePoint
                this.currentTrip.sourcePlaceID = snap.val().sourcePlaceID
                this.currentTrip.destinationPoint = snap.val().destinationPoint
                this.currentTrip.destinationPlaceID = snap.val().destinationPlaceID

                 this.$router.push('/home');
           })
     },
    setPoint(val){
        console.log("===============setPOINT()====================")
        console.log(val)
        console.log("===============END setPOINT()====================")

        
        this.currentTrip.mapInit = 'changed'
        if(this.input_focussed == 'source'){
            this.$refs.source.value = val.name
            this.currentTrip.source = val.name

            console.log(val)
            this.currentTrip.sourcePoint = {
                latitude: val.geometry.location.lat,

                longitude: val.geometry.location.lng
            };
            this.currentTrip.sourcePlaceID = val.place_id
            if(this.currentTrip.destination == ""){
                this.$refs.destination.focus();
                this.input_focussed = 'destination';
                return;           
            }else{
                this.saveTrip(this.currentTrip)
                this.$router.push('/home');
            } 
            
        }
        if(this.input_focussed == 'destination'){
            // alert("HUh! Im also here")
            this.$refs.destination.value = val.name
            this.currentTrip.destination = val.name
            this.currentTrip.destinationPoint = {
                latitude: val.geometry.location.lat,

                longitude: val.geometry.location.lng
            };
            this.currentTrip.destinationPlaceID = val.place_id
            if(this.currentTrip.source == ""){
                this.$refs.source.focus();
                this.input_focussed = 'source';
                return;           
            }else{
                this.saveTrip(this.currentTrip)
                this.$router.push('/home');
            } 
        }
    },
    setPlace(place) {
            console.log("===============SETPLACE()====================")
            console.log(place)
            console.log("===============END SETPLACE()====================")

            this.currentTrip.source = this.$refs.source.value;
            this.currentTrip.mapInit = 'changed'
            this.input_focussed == 'destination'
            // if (this.sourceMarker != null) {
            //     this.sourceMarker.setMap(null);
            // }

            console.log("====================================");

            console.log(this.$refs.source.value);

            console.log("====================================");

            let position = {
                lat: place.geometry.location.lat(),

                lng: place.geometry.location.lng()
            };

            
            this.currentTrip.sourcePoint = {
                latitude: place.geometry.location.lat(),

                longitude: place.geometry.location.lng()
            };
            this.currentTrip.sourcePlaceID = place.place_id
            this.$refs.destination.focus()
             if(this.currentTrip.destination == ""){
            this.$refs.destination.focus();
            }else{
            this.saveTrip(this.currentTrip)
            this.$router.push('/home');
            
            }  
        },
    setDestination(place) {
         console.log("===============SETDESTINATION()====================")
            console.log(place)
            console.log("===============END SETDESTINATION()====================")

            
            this.currentTrip.destination = this.$refs.destination.value;
            this.currentTrip.mapInit = 'changed'
            this.input_focussed == 'source'
            // if (this.destinationMarker != null) {
            //     this.destinationMarker.setMap(null);
            // }

            console.log("====================================");

            console.log(this.$refs.destination.value);

            console.log("====================================");

            let position = {
                lat: place.geometry.location.lat,

                lng: place.geometry.location.lng
            };

            
            this.currentTrip.destinationPoint = {
                latitude: place.geometry.location.lat(),

                longitude: place.geometry.location.lng()
            };
            this.currentTrip.destinationPlaceID = place.place_id

        if(this.currentTrip.source == ""){
            this.$refs.source.focus();
        }else{
        this.saveTrip(this.currentTrip)
        this.$router.push('/home');
        }
        },

 },
 created(){

    this.$store.dispatch('getAllTrips')
   
    GoogleMapsLoader.load(google => {
      getLocation(location => {
        const self = this;
         let geocoder = new google.maps.Geocoder();
        var position = {}
            position = {
             lat: location.coords.latitude,

             lng: location.coords.longitude
            }; //End

        this.axios.get('https://maps.googleapis.com/maps/api/place/nearbysearch/json?location='+location.coords.latitude+','+location.coords.longitude+'&radius=50000&type=airport&key=AIzaSyBwNhtFQMlUzChkztqeQ3dI9Xz4CNEWEP0').then(
          response => {
            console.log('============Response========================');
            console.log(response.data);
            console.log('====================================');

            this.NearbyAirports = response.data.results;
            this.flightempty = false
          }
        ).catch(err => {
          console.log('===============ERROR=====================');
          console.log(err);
          console.log('====================================');
          this.flightempty = true
        })
         this.axios.get('https://maps.googleapis.com/maps/api/place/nearbysearch/json?location='+location.coords.latitude+','+location.coords.longitude+'&rankby=distance&type=lodging&key=AIzaSyBwNhtFQMlUzChkztqeQ3dI9Xz4CNEWEP0').then(
          response => {
            console.log('============Response========================');
            console.log(response.data);
            console.log('====================================');

            this.NearbyHotels = response.data.results;
            this.hotelempty = false
          }
        ).catch(err => {
          console.log('===============ERROR=====================');
          console.log(err);
          console.log('====================================');
          this.hotelempty = true
        })
        let map = new google.maps.Map(document.getElementById("mapmy"), {
          zoom: 15,

          center: position,

          styles: styles.silver
        });
        this.mymap = map;
        this.myLocmarker = new google.maps.Marker({
          flat: true,

          icon: {
            path: google.maps.SymbolPath.CIRCLE,

            scale: 8,

            fillOpacity: 0.8,

            strokeColor: "gold",

            strokeWeight: 5
          },

          map: this.mymap,

          optimized: false,

          position: position,

          title: "location",

          visible: true
        }); //end Marker Icon Location Image
        this.pin = new google.maps.Marker({
          icon: {
            path: google.maps.SymbolPath.BACKWARD_CLOSED_ARROW,

            scale: 5,

            fillOpacity: 0.8,

            strokeColor: "gold",

            strokeWeight: 1
          },

          map: map,

          position: map.getCenter(),

          title: "Pin"
        });
        this.mymap.setCenter(position);
        this.myLocmarker.setPosition(position);
        this.pin.setPosition(position);
        var autocomplete = new google.maps.places.Autocomplete(
          document.getElementById("pac-input")
        );
        autocomplete.addListener("place_changed", () => {
          let place = autocomplete.getPlace();
         console.log("===============SOURCE=====================");
         console.log(place);
         console.log("====================================");
         self.setPlace(place);

        });
        var autocompleteDestination = new google.maps.places.Autocomplete(
          document.getElementById("pac-input-destination")
        );

        autocompleteDestination.addListener("place_changed", () => {
          let place = autocompleteDestination.getPlace();
          console.log('===========Destination Changed=========================');
          console.log(place);
          console.log('====================================');
          self.setDestination(place);
        });

        let locpin = new google.maps.Marker({
          icon: {
            path: google.maps.SymbolPath.CIRCLE,

            scale: 5,

            fillOpacity: 0.8,

            strokeColor: "gold",

            strokeWeight: 2
          },

          map: map,

          position: map.getCenter(),

          title: "Pin"
        });

        map.addListener("dragend", e => {
          let pos = {
            lat: map.getCenter().lat(),

            lng: map.getCenter().lng()
          };

          geocoder.geocode(
            {
              location: pos
            },

            function(results, status) {
              if (status === "OK") {
                if (results[0]) {
                  console.log('================GOECODED MESSAGE====================');

                  console.log(results[0]);

                  console.log('====================================');
                locpin.setPosition(map.getCenter()); 
                self.location_details = results[0].formatted_address;

                //   self.location_data = results[0]; 
                } else {
                  window.alert("No results found"); //TODO handle this later on
                }
              } else {
                window.alert("Geocoder failed due to: " + status); //TODO also handle this later
              }
            }
          );
        });

        map.addListener("drag", function(e) {
          // this.pin_dest = map.getCenter();

          let pos = {
            lat: map.getCenter().lat(),

            lng: map.getCenter().lng()
          };

          // console.log('==================fgfgfgfg==================');

          // console.log(JSON.stringify(map.getCenter()));

          // console.log('====================================');

          locpin.setPosition(map.getCenter()); //Updates the pin location to the center of map

        //   self.pin_dest = pos;

          // console.log('====================================');

          // console.log(map.getCenter());

          // console.log(self.pin_dest);
          // console.log('====================================');
        }); //End the map listener
      }); //End getLocation
    }); //End GooglemapsLoader

 },
}
</script>

<style scoped>

.md-app {
    display: -webkit-box;
    display: flex;
    overflow: hidden;
    position: relative;
    height: 100vh !important;
}
.md-app .md-content.md-theme-default {
    margin: 0 !important;
}
*:focus { outline: none !important; }
li {
    display: inline-block;
    margin: 0 !important;
}
* {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);  
} 
.md-app-content .md-card {
    margin-right: 0 !important;
    margin-left: 0 !important;
    overflow: visible;
}
.md-app-content {
    padding: 0px !important;
}
.form-control {
    width: 100%;
    padding: 10px;
    margin: 4px;
    border: 1px solid #f2f2f2;
    background: #f2f2f2;
}
.popup-tip-anchor {
  height: 0;

  position: absolute;
}

/* The bubble is anchored above the tip. */

.popup-bubble-anchor {
  position: absolute;

  width: 100%;

  bottom: 8px;

  left: 0;
}

/* Draw the tip. */

.popup-bubble-anchor::after {
  content: "";

  position: absolute;

  top: 0;

  left: 0;

  /* Center the tip horizontally. */

  transform: translate(-50%, 0);

  /* The tip is a https://css-tricks.com/snippets/css/css-triangle/ */

  width: 0;

  height: 0;
}

/* The popup bubble itself. */

.popup-bubble-content {
  position: absolute;

  top: 0;

  left: 0;

  transform: translate(-50%, -100%);
}

.pin-lock {
  position: absolute;

  height: 60px;

  left: 42.1%;

  top: 37%;

  padding-left: 60px;
}

.loc_pin {
  position: absolute;

  height: 50px;

  left: 0%;

  top: 0%;

  width: 200px;

  /* border-radius: 50%; */

  /* background: #000000; */

  border-left: 8px groove black;

  /* opacity: .8; */

  text-align: center;

  padding: px;

  margin: -55px 1px 0px -140px;
}

.loc_pin2 {
  position: absolute;

  height: 50px;

  left: 0%;

  top: 0%;

  width: 200px;

  /* border-radius: 50%; */

  /* background: #000000; */

  border-left: 8px groove gold;

  /* opacity: .8; */

  text-align: center;

  padding: px;

  margin: -55px 1px 0px -140px;
}

.pin {
    position: absolute;
    height: 60px;
    left: 0%;
    top: 5px;
    width: 60px;
    /* border-radius: 50%; */
    background: #000000;
    /* border: 2px solid gold; */
    opacity: 1;
    text-align: center;
    padding: 6px;
    border-radius: 50px;
  padding: 6px;
}

.line {
  height: 70px;

  left: 50%;

  top: 50%;

  background: #000000;

  width: 3px;

  /* border-radius: 20%; */

  position: absolute;
}
.arrow-down {
  width: 0;
    height: 0;
    position: absolute;
    top: 60px;
    left: 22px;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 25px solid #000000;
}
.bottomPin {
    padding: 15px;
    bottom: 30px;
    position: absolute;
    width: 100%;
    text-align: center;
    font-weight: bold;
}
.card-box {
    padding: 10px;
    background: #fff;
    font-size: xx-small;
        padding: 10px;
    background: #fff;
    width: 100%;
}

</style>



