<template>
   <md-app style="height: 100vh !important">
        <md-app-toolbar class="md-transparent" md-elevation="0">
        <md-button class="md-icon-button toggle-button">
         <router-link to="/home"> <md-icon>arrow_back</md-icon> </router-link>
        </md-button>
        <span class="md-title">Add People</span>
      </md-app-toolbar>
      <md-app-content>
          <md-card class="md-elevation-0">
          <md-card-content style="background: #f2f2f2 !important">
             
            <div class="md-layout md-gutter">
            
            <div class="md-layout-item">
                <md-field>
                    <label for="people">Booking For</label>
                    <md-select v-model="selectedPeople" name="people" id="people" multiple>
                    <md-option v-for="people in chofaPeople" :value="formatPeople(people)" :key="people.id">{{ formatPeople(people) }}</md-option>
                   
                    </md-select>
                </md-field>
             </div>
          </div>
        </md-card-content>
        <md-card-content >
             
            <div class="md-layout md-gutter">
            
            <div class="md-layout-item">
                 <md-ripple>
                     <md-chips v-model="selectedPeople" md-static></md-chips>
                 </md-ripple>
             </div>
          </div>
        </md-card-content>
          </md-card>
           <div class="bottom-bar" @click="confirmPeople">
             <md-card md-with-hover style="background: #020202;color: #FFEB3B;margin-top: 5px;"  >
       <md-ripple>
      <md-card-content>
        Confirm
      </md-card-content>
       </md-ripple>
    </md-card>
         </div>
      </md-app-content>
   </md-app>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import localforage from 'localforage';

export default {
    components: {
       
    },
    mounted(){
        this.selectedPeople = this.currentTrip.clients_arr
        localforage.getItem('access_token').then((value) => {
          console.log('oh say can you see, ' + value);
          self.access_token = value
          if(value == null){
              this.$router.push('/')
          }else{
            this.fetchPeople(value)
          }

        }).catch((err) => {
          console.log('the rockets red glare has blinded me');
        });
    },
    watch: {
       
    },
    computed:{
    ...mapGetters({
      currentTrip: 'getCurrentTrip',
    })
    },
    data() {
        return {
        selectedPeople: [],
        access_token: null,
        chofaPeople: []

        }
    },
    methods: {
        confirmPeople(){
            this.currentTrip.clients_arr = this.selectedPeople
            this.currentTrip.clients = this.selectedPeople.toString();
          
            this.$router.push('/home');
        },
        formatPeople(people){
            return people.name + " " + people.lastname
        },
        fetchPeople(access_token){
          let self = this
          this.axios({
            method: 'post',
            url: 'http://localhost:8000/api/allusers',
            headers: {
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept",
              "Authorization": 'Bearer '+access_token,
              "Accept": "application/json"
            }
            }).then(function (response) {
              console.log(response.data);
             self.chofaPeople = response.data.users
              
            })
            .catch(function (error) {
              console.log("Post Error : " +error);
              
            });
        }
    }

}
</script>

<style scoped>
.md-app .md-content.md-theme-default, .md-field {
    margin: 0 !important;
}

*:focus { outline: none !important; }
li {
    display: inline-block;
    margin: 0 !important;
}
* {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);  
} 
.md-field.md-theme-default:after {
    background-color: #fff !important;
}
.md-app {
    display: -webkit-box;
    display: flex;
    overflow: hidden;
    position: relative;
    height: 100vh !important;
}
.md-app-content .md-card {
    margin-right: 10px !important;
    margin-left: 10px !important;
    overflow: visible;
}
.md-app-content {
    padding: 0px !important;
}
.bottom-bar {
	position: fixed;
    bottom: 10px;
    width: 90%;
    margin: 5%;
    text-align: center;
	/* background: #000; */
}
.form-control {
    width: 100%;
    padding: 10px;
    margin: 4px;
    border: 1px solid #f2f2f2;
    background: #f2f2f2;
}
</style>



