import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'


Vue.use(VueAxios, axios)
let _trips = [
    {"id": 1, "source": "Delta towers", "destination": "Jomo Kenyatta International airport"}
  ]
  
  export default {
    getTrips (token,cb) {
      // console.log("===========================================************************")
      // console.log(token)
      // console.log("===========================================************************")
      
      Vue.axios({
        method: 'get',
        url: 'http://localhost:8000/api/mytrips',
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept",
          "Authorization": 'Bearer '+token,
          "Accept": "application/json"
        }
        })
      .then(response => {
        // this.reset();
        
       cb(response.data)
         })
     .catch(error => {     
         this.errors = [];   
          console.log('====================================');  
          console.log('Error');   
             
      });
      cb(_trips)
     
    },
    saveTripSQL(payload,cb) {
        // console.log(payload.ride.tripToken)
      Vue.axios({
        method: 'post',
        url: 'http://localhost:8000/api/savetrip',
        data:  {
          token: payload.ride.tripToken,
          source: payload.ride.source,
          destination: payload.ride.destination,
          traveller_code: payload.ride.clients,
          approver_code: payload.ride.traveller_code,
          status: payload.ride.status,
          clientadmin_code: payload.ride.traveller_code,
          driver: "0",
          scheduled_at: payload.ride.scheduled_at,
          description: payload.ride.description,
          cost: payload.ride.cost,
          distance: payload.ride.distance },
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept",
          "Authorization": 'Bearer '+payload.token,
          "Accept": "application/json"
        }
        })
      .then(response => {
        // this.reset();
        console.log('============YADA YADA========================');
       console.log(response.data.trip);
       
       cb(response.data.trip)  
     
         })
     .catch(error => {     
         this.errors = [];   
          console.log('====================================');  
          console.log('Error');   
          cb(null) 
      });
     
    }
}


