<template>
  <div class="page-container">
          <md-toolbar class="md-transparent"  v-show="connectivityStatus" md-elevation="0" style="
    font-size:  120%;
    font-weight:  bolder;
    justify-content:  center;
    align-items:  center;
    color:  #666;
">

 <span class="md-title">{{ connectivityText }}</span>
</md-toolbar>
   
    
     <router-view></router-view>
  </div>
</template>

<script>

require('./assets/fonts/font-awesome-4.2.0/css/font-awesome.min.css')

export default {
  name: 'app',
  
  data() {
    return {
      
      menuVisible: false,
      connectivityText: '',
      connectivityStatus: false
    }
  },
  created(){
    var slowLoad = window.setTimeout( function() {
        alert( "the page is taking its sweet time loading" );
    }, 5000 );
    window.addEventListener( 'load', function() {
        window.clearTimeout( slowLoad );
    }, false );
     window.addEventListener('offline', () => {
          this.connectivityStatus = true;
          this.connectivityText = 'You seem to be offline.';
        })
        window.addEventListener('online', () => {
        //  alert('YAYA')
          this.connectivityStatus = false;
        })
  },
  methods: {
  
  }
}

	
</script>

<style>
a {
  color: #666 !important;
  text-decoration: none !important;
}
@media (max-width: 576px)
{
::-webkit-scrollbar { 
    display: none; 
}
}
</style>



