<template>
   <div id="pincode">
     <div class="back">
        <md-button @click="setPin()" class="md-icon-button toggle-button" style="background-color: transparent !important">
         <md-icon>arrow_back</md-icon>
        </md-button>
     </div>
    <div class="table">
      <div class="cell">

        <div id="anleitung">
          <p style="letter-spacing: 4px;">
            <strong>Please enter your PIN-Code.</strong><br> {{ msg }}
          </p>
        </div>

        <div id="fields">
          <div class="grid">
            <div :class="{'grid__col grid__col--1-of-5 numberfield ': true,  active: dot1Active}"><span></span></div>
            <div :class="{'grid__col grid__col--1-of-5 numberfield ': true,  active: dot2Active}"><span></span></div>
            <div :class="{'grid__col grid__col--1-of-5 numberfield ': true,  active: dot3Active}"><span></span></div>
            <div :class="{'grid__col grid__col--1-of-5 numberfield ': true,  active: dot4Active}"><span></span></div>
              <div class="grid__col grid__col--1-of-5"><button style="margin-top: -20px;" @click="popPin()"><i class="fa fa-times"></i></button></div>
          </div>
        </div>

        <div id="numbers">
          <div class="grid">
            <div class="grid__col grid__col--1-of-3"><button @click="pushPin('1')">1</button></div>
            <div class="grid__col grid__col--1-of-3"><button @click="pushPin('2')">2</button></div>
            <div class="grid__col grid__col--1-of-3"><button @click="pushPin('3')">3</button></div>

            <div class="grid__col grid__col--1-of-3"><button @click="pushPin('4')">4</button></div>
            <div class="grid__col grid__col--1-of-3"><button @click="pushPin('5')">5</button></div>
            <div class="grid__col grid__col--1-of-3"><button @click="pushPin('6')">6</button></div>

            <div class="grid__col grid__col--1-of-3"><button @click="pushPin('7')">7</button></div>
            <div class="grid__col grid__col--1-of-3"><button @click="pushPin('8')">8</button></div>
            <div class="grid__col grid__col--1-of-3"><button @click="pushPin('9')">9</button></div>

            <div class="grid__col grid__col--1-of-3"></div>
            <div class="grid__col grid__col--1-of-3"><button @click="pushPin('0')">0</button></div>
            <div class="grid__col grid__col--1-of-3"></div>

          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import localforage from 'localforage';
export default {
    data() {
        return {
           pin: '1234',
           enterCode: "", 
           dot1Active: false,
           dot2Active: false,
           dot3Active: false,
           dot4Active: false,
           lengthCode: 0,
           msg: ''
        }
    },
    methods: {
        pushPin(clickedNumber){
            // console.log(val);
            this.enterCode = this.enterCode + clickedNumber;
            this.updateLengthCode();
              
            
            
        },
        setPin(){
          let self = this
           localforage.setItem('access_token', null).then((v) => {
                console.log('woot! we saved access token' + v);
              
              }).catch((err) => {
                //  self.$router.push('home')
              });
          localforage.setItem('access_pin', false).then((val) => {
                console.log('woot! we saved pin ' + val);
                self.$router.push('/')
              }).catch((err) => {
                //  self.$router.push('home')
              });
          
        },
        updateLengthCode(){
            this.lengthCode = parseInt(this.enterCode.length);
            this.lengthCode--;
            console.log(this.lengthCode)
            console.log(this.enterCode)
            switch(this.lengthCode){
                    case 0:
                    this.dot1Active= true
                    this.dot2Active= false
                    this.dot3Active= false
                        break;
                    case 1:
                    this.dot2Active= true
                    this.dot3Active= false
                     this.dot4Active=false
                        break;
                    case 2:
                    this.dot3Active= true
                    this.dot4Active=false
                        break;
                    case 3:
                        this.dot4Active=true
                        if(this.enterCode == this.pin){
                            this.msg = 'PIN SUCCESS'
                            this.$router.push('/home')
                        }else {
                            this.msg = 'Wrong PIN'
                            
                        }
                        this.dot1Active= false
                        this.dot2Active= false
                        this.dot3Active= false
                        this.dot4Active= false
                        this.enterCode = ""
                        break;
                    default:

                        this.dot1Active= false
                        this.dot2Active= false
                        this.dot3Active= false
                        this.dot4Active= false
                        this.enterCode = ""
                }
        },
        popPin(){
          
            this.enterCode = this.enterCode.substring(0, this.enterCode.length - 1);
            this.updateLengthCode();
              
        }
    }

}
</script>

<style>
@import 'https://fonts.googleapis.com/css?family=Titillium+Web:400,700';
body {
  position: relative;
  background-color: #000;
  -webkit-font-smoothing: antialiased;
  height: 100%;
  width: 100%;
}
#restartbtn {
  text-align: center;
  color: #fff;
  cursor: pointer;
  margin: 20px;
}
.table {
  display: table;
  height: 100%;
  width: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.3);
}

.cell {
  display: table-cell;
  vertical-align: middle;
  position: relative;
  width: 100%;
  text-align: center;
}

#pincode button:focus {
  background-color: white;
  color: #333333;
  outline: 0;
}
/*------
# Pincode
----*/

#pincode {
    position: fixed;
    overflow: hidden;
    height: 100%;
    width: 100%;
    z-index: 9999;
    background-color: #000;
    background-image: url('/static/img/watch.jpg');
    background-size: cover;
    background-position: center;
    color: #fff;
    /* border-radius: 10px; */
    text-align: center;
    -webkit-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.6);
    box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.6);
    /* top: 50%; */
    /* left: 50%; */
    /* margin-top: -335px; */
    /* margin-left: -188px; */
}

#numbers {
  max-width: 300px;
  padding: 0 20px;
  margin: 0 auto;
  position: relative;
  display: block;
  -webkit-transition: all 1s ease-out;
  -moz-transition: all 1s ease-out;
  transition: all 1s ease-out;
  opacity: 1;
}

#numbers.hide {
  opacity: 0.3;
}

#pincode button {
  width: 70px;
  height: 70px;
  margin-bottom: 10px;
  background-color: rgba(0, 0, 0, 0.35);
  border: 0;
  color: #fff;
  font-size: 25px;
  line-height: 50px;
  border-radius: 100%;
  opacity: 1;
  outline: 0;
  transition: all .6s ease;
}

#pincode button:active {
  background-color: rgba(0, 0, 0, 0.6);
  outline: 0;
}

#fields {
  max-width: 200px;
  padding: 0 20px;
  margin: 50px auto;
  position: relative;
  display: block;
}

#fields .numberfield {
  text-align: center;
}

#fields .numberfield span {
  height: 10px;
  width: 10px;
  border: 2px solid #fff;
  background-color: transparent;
  border-radius: 100%;
  position: relative;
  display: inline-block;
  text-align: center;
}

#fields .numberfield.active span {
  background-color: #fff;
}

#fields .numberfield.right span {
  background-color: #272a2f;
  border-color: #272a2f;
  transition: all .5s ease-in-out;
  -webkit-transition: all .5s ease-in-out;
}


/*------
# Toast Grid
----*/

.grid {
  list-style: none;
  margin-left: -20px;
}

.grid__col--1-of-3,
.grid__col--2-of-6,
.grid__col--4-of-12 {
  width: 33.33333%;
}

.grid__col--1-of-4,
.grid__col--2-of-8,
.grid__col--3-of-12 {
  width: 25%;
}
.grid__col--1-of-5,
.grid__col--2-of-10,
.grid__col--3-of-15 {
  width: 20%;
}
.grid__col {
  box-sizing: border-box;
  display: inline-block;
  margin-right: -.25em;
  min-height: 1px;
  padding-left: 20px;
  vertical-align: top;
}

.grid__col--centered {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.grid__col--d-first {
  float: left;
}

.grid__col--d-last {
  float: right;
}

.grid--no-gutter {
  margin-left: 0;
  width: 100%;
}

.grid--no-gutter .grid__col {
  padding-left: 0;
}

.grid--no-gutter .grid__col--span-all {
  margin-left: 0;
  width: 100%;
}

.grid__col--ab {
  vertical-align: bottom;
}

.grid__col--am {
  vertical-align: middle;
}

.miss {
  -webkit-animation: miss .8s ease-out 1;
  animation: miss .8s ease-out 1;
}

@-webkit-keyframes miss {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  10% {
    -webkit-transform: translate(-25px, 0);
    transform: translate(-25px, 0);
  }
  20% {
    -webkit-transform: translate(25px, 0);
    transform: translate(25px, 0);
  }
  30% {
    -webkit-transform: translate(-20px, 0);
    transform: translate(-20px, 0);
  }
  40% {
    -webkit-transform: translate(20px, 0);
    transform: translate(20px, 0);
  }
  50% {
    -webkit-transform: translate(-10px, 0);
    transform: translate(-10px, 0);
  }
  60% {
    -webkit-transform: translate(10px, 0);
    transform: translate(10px, 0);
  }
  70% {
    -webkit-transform: translate(-5px, 0);
    transform: translate(-5px, 0);
  }
  80% {
    -webkit-transform: translate(5px, 0);
    transform: translate(5px, 0);
  }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
}

@keyframes miss {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  10% {
    -webkit-transform: translate(-25px, 0);
    transform: translate(-25px, 0);
  }
  20% {
    -webkit-transform: translate(25px, 0);
    transform: translate(25px, 0);
  }
  30% {
    -webkit-transform: translate(-20px, 0);
    transform: translate(-20px, 0);
  }
  40% {
    -webkit-transform: translate(20px, 0);
    transform: translate(20px, 0);
  }
  50% {
    -webkit-transform: translate(-10px, 0);
    transform: translate(-10px, 0);
  }
  60% {
    -webkit-transform: translate(10px, 0);
    transform: translate(10px, 0);
  }
  70% {
    -webkit-transform: translate(-5px, 0);
    transform: translate(-5px, 0);
  }
  80% {
    -webkit-transform: translate(5px, 0);
    transform: translate(5px, 0);
  }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
}

#hastylink {
  position: fixed;
  bottom:5px;
  left: 0;
  right: 0;
  font-size: 13px;
  color: #fff;
  text-decoration: none;
  text-align: center;
}
.back {
      position: absolute;
}
</style>



