<template>
     <md-app style="height: 100vh !important" >
        <md-app-toolbar class="md-primary" md-elevation="0">
        <md-button class="md-icon-button toggle-button">
         <router-link to="/home"> <md-icon>arrow_back</md-icon> </router-link>
        </md-button>
        <span class="md-title">My Rides</span>
      </md-app-toolbar>
      <md-app-content style="margin-top: -1px; margin-left: 0px !important;" v-touch:swipe="swipeHandler">
         <md-tabs md-alignment="fixed" class="md-primary" :md-active-tab="active">
      <md-tab id="tab-past" ref="tabs" md-label="Past" md-icon="history">
                    <md-empty-state
                    v-if="history_trips.length < 1"
                    md-icon="history"
                    md-label="My Trips"
                    md-description="Hi! your saved trips will appear here.">
                    </md-empty-state>

          <md-list class="md-triple-line"  v-if="history_trips.length > 0" style="width: 95% !important;">
              <div v-for="trip in history_trips" :key="trip.id">
                <md-list-item  @click="viewTrip(trip)" style="width: 100%">
                        <md-avatar>
                        <md-icon>local_taxi</md-icon>
                        </md-avatar>

                        <div class="md-list-item-text">
                        <span>{{ trip.source }}</span>
                        <span>{{ trip.destination }}</span>
                        <p>Ksh {{ trip.cost }} </p>
                        </div>

                        <md-button class="md-icon-button md-list-action">
                        <md-icon style="color: #02ca02;">done_all</md-icon>
                        </md-button>
                    </md-list-item>

                    <md-divider class="md-inset"></md-divider>
              </div>
    
      

    </md-list>
      </md-tab>
      <md-tab id="tab-upcoming" md-label="Upcoming" md-icon="loop" >
           <md-empty-state
                    md-icon="loop"
                    md-label="Upcoming Trips"
                    md-description="Hi! your pending trips will appear here.">
                    </md-empty-state>
      </md-tab>
    </md-tabs>

      </md-app-content>
    
   </md-app>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Vue from 'vue'
import Vue2TouchEvents from 'vue2-touch-events'

import localforage from 'localforage';
 
Vue.use(Vue2TouchEvents)
export default {
 data() {
     return {
         active: 'tab-past',
         access_token: null,
         history_trips: [],
         loadedlist: true
     }
 },
 watch: {
     
      trips(val){
        //  console.log('=======Changed TRips========')
        // console.log(val)
        // console.log('=======END OF SOMETHING GREAT========')
        localforage.setItem('history_trips',val).then((value) => {
                console.log('woot! we saved history trips');
              
              }).catch((err) => {
                //  self.$router.push('home')
              });
      },
      history_trips(val){
        //    console.log('======HISTORY TRIPS========')
        // console.log(val)
        // console.log('=======END OF SOMETHING GREAT========')
      }
    },
computed:{
    ...mapGetters({
      trips: 'allTrips',
    })
  },
  created(){

  },
  mounted(){
    //   console.log(this.trips)
    this.$store.dispatch('getAllTrips')
    let self = this
      this.loadedlist = false
    this.active = 'tab-upcoming'
    setTimeout(()=> {
        // console.log(value)
        this.active = 'tab-past'
            this.loadedlist = true
    },1000)
         localforage.getItem('history_trips').then((value) => {
         
          if(value == null){
              console.log('NOthing in history trips, duh')
          }else{
            self.history_trips = value.sort(function(a,b) {return (a.id < b.id) ? 1 : ((b.id < a.id) ? -1 : 0);} )
            
          }

        }).catch((err) => {
          console.log('the rockets red glare has blinded me');
        });
        
      localforage.getItem('access_token').then((value) => {
          // console.log('oh say can you see, ' + value);
          self.access_token = value
          if(value == null){
              self.$router.push('/')
          }else{
            // alert('Unauthorised')
            this.$store.dispatch('getAllTrips',value)
          }

        }).catch((err) => {
          console.log('the rockets red glare has blinded me');
        });
  },
 methods: {
     viewTrip(trip){
         this.$router.push('ride/'+trip.token)
     },
        swipeHandler(dir){
            console.log(dir)
            switch(dir){
                case 'right':
                    this.active = 'tab-past'
                    break;
                case 'left':
                this.active = 'tab-upcoming'
                    break
                default:
                this.active = 'tab-past'
            }
        },
        handleTabChange(val){
            console.log(this.$refs.tabs)

        }
 }
}
</script>

<style>
.md-app-content {
    padding: 0px !important;
}
.md-toolbar.md-theme-default.md-primary {
    background-color: #000000 !important;
    background-color: var(--md-theme-default-primary, #000000) !important;
    color: #fff;
    color: var(--md-theme-default-text-primary-on-primary, #fff);
}

  .md-app {
    display: -webkit-box;
    display: flex;
    overflow: hidden;
    position: relative;
    height: 100vh !important;
}
</style>



