<template>
<div class="container">
      <md-ripple style="height: 35vh !important">
    <div class="card-boxed bg">
      
  <label class="label head" style="padding-top: 20vh !important;">
    <div class="label-text" style="color:#fff !important">CHOFA | LOGIN</div>
    <small style="color:#f2f2f2 !important">Your Professional Riding Partner</small>
  </label>
        

             
    </div>
    </md-ripple>
  <div class="card-boxed">
  <form>
  <label>
    <input type="text" v-model="email" required />
    <div class="label-text">Email Address</div>
  </label>
  <label>
    <input type="password" v-model="password" required />
    <div class="label-text">Password</div>
  </label>
  <label style="position: absolute; left: 40%">
 <half-circle-spinner v-show="loading"
    :animation-duration="1000"
    :size="60"
    color="gold"
  />
  </label>
   
  <button v-show="!loading" @click.prevent="Login()"><i class="fa fa-sign-in"></i> Sign In</button>
</form>
  </div>
  <div class="card-boxed">

<router-link to="pinlock" class="label" v-show="!loading" style="
    color: rgb(196, 222, 25) !important;
    padding-top:  0px !important;
    font-size: 40px;
    text-align: center
">
<md-ripple>
     <i class="fab fa-buromobelexperte"></i>

</md-ripple>
</router-link>


     
    </div>
    <simplert :useRadius="true"
          :useIcon="true"
          ref="infomodal">
</simplert>
</div>

</template>

<script>
import localforage from 'localforage';
import Simplert from 'vue2-simplert';
import { HalfCircleSpinner } from 'epic-spinners'
export default {
    data(){
        return {
            isActive: true,
            email: '',
            password:'',
            loading: false,
            
        }
    },
     components: {
    Simplert,HalfCircleSpinner
    },
    mounted(){
        // this.$refs.username.focus()
        let self = this
        localforage.getItem('access_token').then((value) => {
          console.log('oh say can you see, ' + value);

          localforage.getItem('access_pin').then((val) => {
          console.log('oh say can you see, ' + val);
          if(value != null){
             if(val == true){
                  self.$router.push('pinlock')
              }else{
                self.$router.push('home')
              }    
          }else{
            // alert('Unauthorised')
          }
           }).catch((e) => {
          console.log('the rockets red glare has blinded me on PIn!!');
        });
          

        }).catch((err) => {
          console.log('the rockets red glare has blinded me on Access');
        });
    },
    methods: {
        Login(){
            this.loading = true
            this.autoLogin();
            
        },
        autoLogin(){
          let self = this
          this.axios({
            method: 'post',
            url: 'http://localhost:8000/api/login',
            data: {
              "email": this.email,
              "password": this.password,
            },
            headers: {
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept",
              "Accept": "application/json"
            }
            }).then(function (response) {
              console.log(response);
             
              localforage.setItem('access_token', response.data.token).then((value) => {
                console.log('woot! we saved ' + value);
                self.loading = false
                    self.$router.push('home')
                
              }).catch((err) => {
                //  self.$router.push('home')
              });
              localforage.setItem('access_pin', true).then((val) => {
                console.log('woot! we saved pin ' + val);
                
              }).catch((err) => {
                //  self.$router.push('home')
              });
            })
            .catch(function (error) {
              console.log("Post Error : " +error);
              self.launchInfoModal('Sorry, the credentials you entered do not match our records');
              self.loading = false
            });
        },
        launchInfoModal(message){
       let obj = {
         title: 'Invalid credentials',
          message: message,
          type: 'error',
          customCloseBtnClass: 'md-button',
          }
          this.$refs.infomodal.openSimplert(obj)
        },
    }
}
</script>

<style scoped>
body {
  background-color: #333333;
  font-family: "Open Sans", Helvetica;
}

form {
  text-align: center;
}
 .bg {
        background-image: url('/static/img/seat-leon.jpg') !important;
        background-size: cover !important;
        height: 100vh;

    }
 @media screen and (max-width: 500px) {
        .bg {
        background-image: url('/static/img/seat-leon.jpg') !important;
        background-size: cover !important;
        height: 35vh;

    }
}
label {
  display: block;
  letter-spacing: 4px;
  padding-top: 30px;
  text-align: center;
}
.container {
    background: #000;
    height: 100vh;
}
.card-boxed {
    /* background: #000; */
    padding: 30px;
}
label .label-text {
  color: #9B9B9B;
  cursor: text;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  -moz-transform: translateY(-34px);
  -ms-transform: translateY(-34px);
  -webkit-transform: translateY(-34px);
  transform: translateY(-34px);
  transition: all 0.3s;
}
label input {
  background-color: transparent;
  border: 0;
  border-bottom: 2px solid #4A4A4A;
  color: white;
  font-size: 22px;
  letter-spacing: -1px;
  outline: 0;
  padding: 5px 20px;
  text-align: center;
  transition: all 0.3s;
  width: 200px;
}
label input:focus {
  max-width: 100%;
  width: 400px;
}
label input:focus + .label-text {
  color: #F0F0F0;
  font-size: 13px;
  -moz-transform: translateY(-74px);
  -ms-transform: translateY(-74px);
  -webkit-transform: translateY(-74px);
  transform: translateY(-74px);
}
label input:valid + .label-text {
  font-size: 13px;
  -moz-transform: translateY(-74px);
  -ms-transform: translateY(-74px);
  -webkit-transform: translateY(-74px);
  transform: translateY(-74px);
}

button {
  background-color: #000;
  border: 1px solid #f2f2f2;
  color: #f2f2f2;
  cursor: pointer;
  font-size: 20px;
  margin-top: 20px;
  padding: 15px 40px;
  text-transform: uppercase;
  transition: all 200ms;
}
button:hover, button:focus {
  background-color: white;
  color: #333333;
  outline: 0;
}


</style>



