   methods: {
     //START REGISTERING THE NOTIFICATION
     /**
     * Register the service worker.
     */
   
    registerServiceWorker () {
      if (!('serviceWorker' in navigator)) {
     //   console.log('Service workers aren\'t supported in this browser.')
        return
      }

      navigator.serviceWorker.register('/service-worker.js')
        .then(() => this.initialiseServiceWorker())
    },

    initialiseServiceWorker () {
      if (!('showNotification' in ServiceWorkerRegistration.prototype)) {
      //  console.log('Notifications aren\'t supported.')
        return
      }

      if (Notification.permission === 'denied') {
     //   console.log('The user has blocked notifications.')
        return
      }
      if (!('PushManager' in window)) {
     //   console.log('Push messaging isn\'t supported.')
        return
      }


      navigator.serviceWorker.ready.then(registration => {
        registration.pushManager.getSubscription()
          .then(subscription => {
            if (!subscription) {
              // this.$refs.notify.open();
              console.log('THIS WILL PROMPT USER')
              
              // return
            }

            this.updateSubscription(subscription)

          })
          .catch(e => {
           // console.log('Error during getSubscription()', e)
          })
      })
    },

    /**
     * Subscribe for push notifications.
     */
    subscribe () {
      navigator.serviceWorker.ready.then(registration => {
        const options = { userVisibleOnly: true }
        const vapidPublicKey = this.vapidPublicKey
        

        if (vapidPublicKey) {
          options.applicationServerKey = this.urlBase64ToUint8Array(vapidPublicKey)
        }

        registration.pushManager.subscribe(options)
          .then(subscription => {
              this.notify = true

            localforage.setItem('has_enabled', true).then((value) => {
              console.log("Saved Notification key successfully");
              }).catch((err) => {
                //  self.$router.push('home')
              });
            // console.log('====================================');
            // console.log(subscription);
            // console.log('====================================');
           this.updateSubscription(subscription)
          })
          .catch(e => {
            if (Notification.permission === 'denied') {
             // console.log('Permission for Notifications was denied')
            } else {
             // console.log('Unable to subscribe to push.', e)
            }
          })
      })
    },

    /**
     * Unsubscribe from push notifications.
     */
    unsubscribe () {
      navigator.serviceWorker.ready.then(registration => {
        registration.pushManager.getSubscription().then(subscription => {
          if (!subscription) {


            return
          }

          subscription.unsubscribe().then(() => {
            this.deleteSubscription(subscription)

          }).catch(e => {
            //// console.log('Unsubscription error: ', e)
          })
        }).catch(e => {
         // console.log('Error thrown while unsubscribing.', e)
        })
      })
    },

