              "Accept": "application/json"
            }
            }).then(response => {
          if (response.data.status == "OK") {
            let distanceInKM = response.data.rows[0].elements[0].distance.value / 1000;
            let min = response.data.rows[0].elements[0].duration.value / 60;
            console.log("================FARE ESTIMATE=========================")
            console.log('DISTANCE '+distanceInKM+" Kilometers")
            self.distance_in_km = Math.round(distanceInKM)
            console.log('MINUTES '+min+" min")
            self.minutes = Math.round(min)
            self.fareestimate =(Math.round(distanceInKM) * rate + Math.round(min) * 5);
            console.log(self.fareestimate)
            console.log("==================================================")
            // self.loading_fare = false
            

            
          }
            

           
         
