        //   distance: 
        //   4.844
        //   driver
        //   has_prefered: 
        //   false
        //   paid: 
        //   true
        //   phone
        //   scheduled_at: 
        //   "2018-04-09T15:45:52.398+03:00"
        //   source: 
        //   "Delta Offices, Rhapta Road, Nairobi, Kenya"
        //   sourcePoint
        //   status: 
        //   "confirmed"
        //   traveller_code: 
        //   "PwCKeADMIN"
        //   tripCapacity: 
        //   4
        //   tripToken: 
        //   "-L9eRNa5iCsYuMmvT-TD"
        //   trip_details
        //   trip_type: 
        //   "now"
        //   trip_url: 
        //   "client/trip/32"
        //   user_id: 
        //   1

      },
     pushPlaces(){
       this.$router.push('places')
     },
     pushPeople(){
        this.$router.push('people')
     },
     toggleCategory(){
       this.choseRide = !this.choseRide
     },
     changeSchedule(){
       this.$router.push('schedule')
     },
     launchFareModal(){
       let obj = {
         title: 'Ksh '+this.fareestimate,
          message: `
          <div style="color: #666 !important;font-family:  monospace;">
          <div class="md-layout md-gutter">
            <div class="md-layout-item md-layout md-gutter">
             
              <div class="md-layout-item"> (${ this.distance_in_km } KM - ${ this.minutes } Minutes) </div>
              </div>
              </div>
               <div class="md-layout md-gutter">
            <div class="md-layout-item md-layout md-gutter">
              <div class="md-layout-item">Ksh 55/= per KM</div>
              </div>
              </div>
               <div class="md-layout md-gutter">
            <div class="md-layout-item md-layout md-gutter">
              
              <div class="md-layout-item">Ksh 5/= per Min</div>
              </div>
              </div>
          </div>
          
          `,
          customCloseBtnClass: 'md-button',
          }
          this.$refs.faremodal.openSimplert(obj)
     },
    ...mapActions(['testActions']),
    	open: function () {
        console.log('slideoutOpen')
      },
      drawPolyline(source, destination) {
          let route = [source, destination];

          if (this.routepath != null) {
            this.routepath.setMap(null);

            // this.destinationMarker.setMap(null);
          }

          this.routepath = new google.maps.Polyline({
            path: route,

            geodesic: true,

            strokeColor: "#000000",

            strokeOpacity: 0.2,

            strokeWeight: 3
          }); //Polyline drawn

          this.routepath.setMap(this.mymap);

          var directionsDisplay = new google.maps.DirectionsRenderer({
            suppressMarkers: true,
            polylineOptions: {
            strokeColor: '#000000'
            },
          });
          directionsDisplay.setMap(null);
          directionsDisplay.setMap(this.mymap)
          var directionsService = new google.maps.DirectionsService();
            
            
            var request = {
              origin: source, 
              destination: destination,
              travelMode: google.maps.DirectionsTravelMode.DRIVING
            };
        
            directionsService.route(request, function(response, status) {
              if (status == google.maps.DirectionsStatus.OK) {
                directionsDisplay.setDirections(response);
              }
            });

          this.zoomToObject(this.routepath);
        },

        zoomToObject(obj) {
          var bounds = new google.maps.LatLngBounds();

          var points = obj.getPath().getArray();

          for (var n = 0; n < points.length; n++) {
            bounds.extend(points[n]);
          }

          this.mymap.fitBounds(bounds);
        },
      createPopUpClass() {
          var Popup;

          Popup = function(position, content) {
            this.position = position;

            content.classList.add("popup-bubble-content");

            var pixelOffset = document.createElement("div");

            pixelOffset.classList.add("popup-bubble-anchor");

            pixelOffset.appendChild(content);

            this.anchor = document.createElement("div");

            this.anchor.classList.add("popup-tip-anchor");

            this.anchor.appendChild(pixelOffset);

            // Optionally stop clicks, etc., from bubbling up to the map.

            this.stopEventPropagation();
          };

          // NOTE: google.maps.OverlayView is only defined once the Maps API has

          // loaded. That is why Popup is defined inside initMap().

          Popup.prototype = Object.create(google.maps.OverlayView.prototype);

          /** Called when the popup is added to the map. */

          Popup.prototype.onAdd = function() {
            this.getPanes().floatPane.appendChild(this.anchor);
          };

          /** Called when the popup is removed from the map. */

          Popup.prototype.onRemove = function() {
            if (this.anchor.parentElement) {
              this.anchor.parentElement.removeChild(this.anchor);
            }
          };

          /** Called when the popup needs to draw itself. */

          Popup.prototype.draw = function() {
            var divPosition = this.getProjection().fromLatLngToDivPixel(
              this.position
            );

            // Hide the popup when it is far out of view.

            var display =
              Math.abs(divPosition.x) < 4000 && Math.abs(divPosition.y) < 4000
                ? "block"
                : "none";

            if (display === "block") {
              this.anchor.style.left = divPosition.x + "px";

              this.anchor.style.top = divPosition.y + "px";
            }

            if (this.anchor.style.display !== display) {
              this.anchor.style.display = display;
            }
          };

          /** Stops clicks/drags from bubbling up to the map. */

          Popup.prototype.stopEventPropagation = function() {
            var anchor = this.anchor;

            anchor.style.cursor = "auto";

            [
              "click",

              "dblclick",

              "contextmenu",

              "wheel",

              "mousedown",

              "touchstart",

              "pointerdown"
            ].forEach(function(event) {
              anchor.addEventListener(event, function(e) {
                e.stopPropagation();
              });
            });
          };

          return Popup;
       },
      
  
  },
   created(){

      GoogleMapsLoader.load(google => {
      getLocation(location => {
        const self = this;

        var position = {}
            position = {
             lat: location.coords.latitude,

             lng: location.coords.longitude
            }; //End
          if(this.currentTrip.mapInit == 'initial'){
            this.renderMarkers();
      let geocoder = new google.maps.Geocoder();

         geocoder.geocode(
          {
            location: position
          },

          function(results, status) {
            if (status === "OK") {
              if (results[0]) {
                  //  console.log('=================MY LOCATION HERE===================');

                  //  console.log(results[0]);

                  //  console.log('====================================');

                self.location_details = results[0].formatted_address; //Set the initial location deatils

                self.currentTrip.source = results[0].formatted_address;

                self.$refs.source.value = results[0].formatted_address;

                // self.setPlace(results[0]);

                self.currentTrip.sourcePlaceID = results[0].place_id;

                self.currentTrip.sourcePoint = {
                  latitude: results[0].geometry.location.lat(),

                  longitude: results[0].geometry.location.lng()
                };
              } else {
                window.alert("No results found"); //TODO handle this later on
              }
            } else {
              window.alert("Geocoder failed due to: " + status); //TODO also handle this later
            }
          }
        ); //End geocode for the very first time
          }
       

        let map = new google.maps.Map(document.getElementById("mapmy"), {
          zoom: 15,

          center: position,

          styles: styles.silver
        });
        this.mymap = map;
        this.myLocmarker = new google.maps.Marker({
          flat: true,

          icon: {
            path: google.maps.SymbolPath.CIRCLE,

            scale: 8,

            fillOpacity: 0.8,

            strokeColor: "gold",

            strokeWeight: 5
          },

          map: this.mymap,

          optimized: false,

          position: position,

          title: "location",

          visible: true
        }); //end Marker Icon Location Image
        this.pin = new google.maps.Marker({
          icon: {
            path: google.maps.SymbolPath.BACKWARD_CLOSED_ARROW,

            scale: 5,

            fillOpacity: 0.8,

            strokeColor: "gold",

            strokeWeight: 1
          },

          map: map,

          position: map.getCenter(),

          title: "Pin"
        });
        this.mymap.setCenter(position);

        this.myLocmarker.setPosition(position);
        this.pin.setPosition(position);
        if(this.currentTrip.mapInit != 'initial'){
          this.renderMarkers();
           if (this.destinationMarker != null) {
              this.destinationMarker.setMap(null);
            }
            let position = {
              lat: this.currentTrip.destinationPoint.latitude,

              lng: this.currentTrip.destinationPoint.longitude
            };

            let Popup = this.createPopUpClass();

            this.destinationMarker = new google.maps.Marker({
              icon: {
                path: google.maps.SymbolPath.CIRCLE,

                scale: 5,

                fillOpacity: 1
              },

              map: this.mymap,

              position: position
            });

            let popup = new Popup(
              new google.maps.LatLng(position.lat, position.lng),

              document.getElementById("loc_pop2")
            );

            popup.setMap(this.mymap);

            let sourcePosition={
                      lat: this.currentTrip.sourcePoint.latitude,
                      lng: this.currentTrip.sourcePoint.longitude
                    }

            let sourcePop = new Popup(
                    new google.maps.LatLng(sourcePosition.lat, sourcePosition.lng),

                    document.getElementById("loc_pop")
                  ); //Add a popup for the current location

            sourcePop.setMap(this.mymap); //Add the popup to the map
            this.sourceMarker = new google.maps.Marker({
                    flat: true,
                    icon: {
                      path: google.maps.SymbolPath.CIRCLE,
                      scale: 8,
                      fillOpacity: 0.8,
                      strokeColor: "gold",
                      strokeWeight: 5
                    },
                    map: this.mymap,
                    optimized: false,
                    position: sourcePosition,
                    visible: true
                  }); //end Marker Icon Location Image
            this.mymap.setCenter(sourcePosition);
            this.drawPolyline(sourcePosition,position);
         }

      }); //End getLocation
    }); //End GooglemapsLoader

 },
}

</script>


<style>
*:focus { outline: none !important; }
.popup-tip-anchor {
  height: 0;

  position: absolute;
}

/* The bubble is anchored above the tip. */

.popup-bubble-anchor {
  position: absolute;

  width: 100%;

  bottom: 8px;

  left: 0;
}

/* Draw the tip. */

.popup-bubble-anchor::after {
  content: "";

  position: absolute;

  top: 0;

  left: 0;

  /* Center the tip horizontally. */

  transform: translate(-50%, 0);

  /* The tip is a https://css-tricks.com/snippets/css/css-triangle/ */

  width: 0;

  height: 0;
}

/* The popup bubble itself. */

.popup-bubble-content {
  position: absolute;

