<script>
import { mapGetters, mapActions } from 'vuex'
import GoogleMapsLoader from "google-maps";
require('../assets/fonts/font-awesome-4.2.0/css/font-awesome.min.css');
import getLocation from "./utils/GeoLocation";
import Simplert from 'vue2-simplert'
import styles from "./utils/styles";
import localforage from 'localforage';
import firebase from '../firebase'
import { HalfCircleSpinner } from 'epic-spinners'
GoogleMapsLoader.REGION = "KE";
GoogleMapsLoader.KEY = "AIzaSyBwNhtFQMlUzChkztqeQ3dI9Xz4CNEWEP0";
GoogleMapsLoader.LIBRARIES = ["geometry", "places"];


export default {
  name: 'home',
  components: {
    Simplert,
    HalfCircleSpinner
    },
    watch: {
      currentTrip(val){
        console.log('=======START OF SOMETHING GREAT========')
        console.log(val)
        console.log('=======END OF SOMETHING GREAT========')

      }
    },
  computed:{
    ...mapGetters({
      trips: 'allTrips',
      msg: 'getTest',
      currentTrip: 'getCurrentTrip'
    })
  },
  data () {
    return {
      // msg: 'Welcome to Your Vue.js PWA'
       mymap: null,
      myLocmarker: null,
      pin: null,
      destinationMarker: null,
      sourceMarker: null,
      routepath: null,
      driverMarker: null,
      access_token: null,
      fareestimate: '0',
      driverPopup: null,
      driverroutepath: null,
      trip: {
        source: '',
        destination: '',
        clients: '',
        cost: 0
      },
      confirmed: false,
      tripkey: null,
      driverphone: '0'
      
    }
  },
  
  mounted(){
   
   
    this.tripkey = this.$route.params.token_id;
    console.log(this.tripkey)
      let self = this
     localforage.getItem('access_token').then((value) => {
          // console.log('oh say can you see, ' + value);
          self.access_token = value
          if(value == null){
              self.$router.push('/')
          }else{
            // alert('Unauthorised')
            // this.fetchUser();
          }

        }).catch((err) => {
          console.log('the rockets red glare has blinded me');
        });
        this.watchTrip()
  },
   methods: {
     watchDriver(dkey){
        console.log(dkey)
       let driverRef = firebase.database().ref('/drivers/' + dkey + '/');

            driverRef.on('value', dsnap => {
              console.log('======================================================wooooot================');
              console.log(dsnap.val())
                let Popup = this.createPopUpClass();

                let driverloc = dsnap.val().location
                this.driverphone = dsnap.val().Phone_Number
                this.driverPopup = new Popup(new google.maps.LatLng(driverloc.lat, driverloc.long), document.getElementById('loc_pop_driver')); //Add a popup for the current location
                this.driverPopup.setMap(this.mymap); //Add the DESTINATION popup to the map
                if (this.driverMarker != null) {
                    this.driverMarker.setMap(null);
                    }
                this.driverMarker = new google.maps.Marker({
                        flat: true,
                        icon: {
                            path: google.maps.SymbolPath.CIRCLE,
                            scale: 11,
                            fillOpacity: 0.8,
                            strokeColor: 'gold',
                            strokeWeight: 4
                        },
                        map: this.mymap,
                        optimized: false,
                        position: {
                            lat: driverloc.lat,
                            lng: driverloc.long
                        },
                        visible: true }); //end Marker Icon Location Image
                var path = [{
                lat: driverloc.lat,
                lng: driverloc.long}, {
                lat: this.trip.sourcePoint.latitude,
                lng: this.trip.sourcePoint.longitude}, {
                lat: this.trip.destinationPoint.latitude,
                lng: this.trip.destinationPoint.longitude}]
                
                this.drawDriverPolyline(path);
            })
     },
       watchTrip(){
           let tripRef = firebase.database().ref('/trips/' + this.tripkey+ '/');
            
           tripRef.on('value', (snap)=> {
             console.log(snap.val())
             let ride = snap.val()
              this.trip = ride
              var Popup = this.createPopUpClass(); //Create the popup class here
                this.sourcePopup = new Popup(new google.maps.LatLng(this.trip.sourcePoint.latitude, this.trip.sourcePoint.longitude), document.getElementById('loc_pop')); //Add a popup for the current location
                this.sourcePopup.setMap(this.mymap); //Add the SOURCE popup to the map
                this.sourceMarker = new google.maps.Marker({
                    flat: true,
                    icon: {
                        path: google.maps.SymbolPath.CIRCLE,
                        scale: 8,
                        fillOpacity: 0.8,
                        strokeColor: 'gold',
                        strokeWeight: 5
                    },
                    map: this.mymap,
                    optimized: false,
                    position: {
                        lat: this.trip.sourcePoint.latitude,
                        lng: this.trip.sourcePoint.longitude
                    },
                    visible: true
                }); //end Marker Icon Location Image
    
                this.destinationPopup = new Popup(new google.maps.LatLng(this.trip.destinationPoint.latitude, this.trip.destinationPoint.longitude), document.getElementById('loc_pop2')); //Add a popup for the current location
                this.destinationPopup.setMap(this.mymap); //Add the DESTINATION popup to the map
                this.destinationMarker = new google.maps.Marker({
                  flat: true,
                  icon: {
                    path: google.maps.SymbolPath.CIRCLE,
                    scale: 8,
                        fillOpacity: 0.8,
                        strokeWeight: 5
                    },
                    map: this.mymap,
                    optimized: false,
                    position: {
                        lat: this.trip.destinationPoint.latitude,
                        lng: this.trip.destinationPoint.longitude
                    },
                    visible: true
                }); //end Marker Icon Location Image
                this.drawPolyline({
                    lat: this.trip.sourcePoint.latitude,
                    lng: this.trip.sourcePoint.longitude
                }, {
                    lat: this.trip.destinationPoint.latitude,
                    lng: this.trip.destinationPoint.longitude
                })
    

             if(ride.status == 'confirmed'){
               this.confirmed = true
               this.watchDriver(ride.driver.driverID)
             }
           })
    
       },
       drawDriverPolyline(mpath) {
    if (this.driverroutepath != null) {
      this.driverroutepath.setMap(null)
                }
                var lineSymbol = {
          path: 'M 0,-1 0,1',
          strokeOpacity: 1,
          scale: 4
        };
      this.driverroutepath = new google.maps.Polyline({
                      path: mpath,
                      geodesic: true,
                      icons: [{
                        icon: lineSymbol,
                        offset: '0',
                        repeat: '20px'
                      }],
                      strokeOpacity: 0.5,
                      strokeWeight: 5
                      });
      this.driverroutepath.setMap(this.mymap);
      this.zoomToObject(this.driverroutepath);
    
            },
     alert(){
      //  alert('Ahoy')
     },
     callDriver(val){
         window.open("tel:+"+val);
     },
    pushRides(){
      this.$router.push('/rides')
    },
    
     launchFareModal(){
       let obj = {
         title: 'HELP',
          message: `
          
          `,
          customCloseBtnClass: 'md-button',
          }
          this.$refs.faremodal.openSimplert(obj)
     },
    ...mapActions(['testActions']),
    	
      drawPolyline(source, destination) {
          let route = [source, destination];

          if (this.routepath != null) {
            this.routepath.setMap(null);

            // this.destinationMarker.setMap(null);
          }

          this.routepath = new google.maps.Polyline({
            path: route,

            geodesic: true,

            strokeColor: "#000000",

            strokeOpacity: 0.2,

            strokeWeight: 3
          }); //Polyline drawn

          this.routepath.setMap(this.mymap);

          var directionsDisplay = new google.maps.DirectionsRenderer({
            suppressMarkers: true,
            polylineOptions: {
            strokeColor: '#000000'
            },
          });
          directionsDisplay.setMap(null);
          directionsDisplay.setMap(this.mymap)
          var directionsService = new google.maps.DirectionsService();
            
            
            var request = {
              origin: source, 
              destination: destination,
              travelMode: google.maps.DirectionsTravelMode.DRIVING
            };
        
            directionsService.route(request, function(response, status) {
              if (status == google.maps.DirectionsStatus.OK) {
                directionsDisplay.setDirections(response);
              }
            });

          this.zoomToObject(this.routepath);
        },

        zoomToObject(obj) {
          var bounds = new google.maps.LatLngBounds();

          var points = obj.getPath().getArray();

          for (var n = 0; n < points.length; n++) {
            bounds.extend(points[n]);
          }
          console.log('BOUNDS')
          if(this.mymap == null){
            location.reload()
          }
          
          this.mymap.fitBounds(bounds);
        },
      createPopUpClass() {
          var Popup;

          Popup = function(position, content) {
            this.position = position;

            content.classList.add("popup-bubble-content");

            var pixelOffset = document.createElement("div");

            pixelOffset.classList.add("popup-bubble-anchor");

            pixelOffset.appendChild(content);

            this.anchor = document.createElement("div");

            this.anchor.classList.add("popup-tip-anchor");

            this.anchor.appendChild(pixelOffset);

            // Optionally stop clicks, etc., from bubbling up to the map.

            this.stopEventPropagation();
          };

          // NOTE: google.maps.OverlayView is only defined once the Maps API has

          // loaded. That is why Popup is defined inside initMap().

          Popup.prototype = Object.create(google.maps.OverlayView.prototype);

          /** Called when the popup is added to the map. */

          Popup.prototype.onAdd = function() {
            this.getPanes().floatPane.appendChild(this.anchor);
          };

          /** Called when the popup is removed from the map. */

          Popup.prototype.onRemove = function() {
            if (this.anchor.parentElement) {
              this.anchor.parentElement.removeChild(this.anchor);
            }
          };

          /** Called when the popup needs to draw itself. */

          Popup.prototype.draw = function() {
            var divPosition = this.getProjection().fromLatLngToDivPixel(
              this.position
            );

            // Hide the popup when it is far out of view.

            var display =
              Math.abs(divPosition.x) < 4000 && Math.abs(divPosition.y) < 4000
                ? "block"
                : "none";

            if (display === "block") {
              this.anchor.style.left = divPosition.x + "px";

              this.anchor.style.top = divPosition.y + "px";
            }

            if (this.anchor.style.display !== display) {
              this.anchor.style.display = display;
            }
          };

          /** Stops clicks/drags from bubbling up to the map. */

          Popup.prototype.stopEventPropagation = function() {
            var anchor = this.anchor;

            anchor.style.cursor = "auto";

            [
              "click",

              "dblclick",

              "contextmenu",

              "wheel",

              "mousedown",

              "touchstart",

              "pointerdown"
            ].forEach(function(event) {
              anchor.addEventListener(event, function(e) {
                e.stopPropagation();
              });
            });
          };

          return Popup;
       },
  },
   created(){

      // this.$store.dispatch('getAllTrips')

      GoogleMapsLoader.load(google => {
      getLocation(location => {
        const self = this;

        var position = {}
            position = {
             lat: location.coords.latitude,

             lng: location.coords.longitude
            }; //End
        
       

        let map = new google.maps.Map(document.getElementById("mapmy"), {
          zoom: 15,

          center: position,

          styles: styles.silver
        });
        this.mymap = map;
        this.myLocmarker = new google.maps.Marker({
          flat: true,

          icon: {
            path: google.maps.SymbolPath.CIRCLE,

            scale: 8,

            fillOpacity: 0.8,

            strokeColor: "gold",

            strokeWeight: 5
          },

          map: this.mymap,

          optimized: false,

          position: position,

          title: "location",

          visible: true
        }); //end Marker Icon Location Image
        this.pin = new google.maps.Marker({
          icon: {
            path: google.maps.SymbolPath.BACKWARD_CLOSED_ARROW,

            scale: 5,

            fillOpacity: 0.8,

            strokeColor: "gold",

            strokeWeight: 1
          },

          map: map,

          position: map.getCenter(),

          title: "Pin"
        });
        this.mymap.setCenter(position);

        this.myLocmarker.setPosition(position);
        this.pin.setPosition(position);

        

      }); //End getLocation
    }); //End GooglemapsLoader

 },
}

</script>


